import { boundChildList, removeBindChildren } from '@/api/parents-and-children'
import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'

export default {
    name: 'slide-list',
	components: {
	    uniSearchBar
	},
    computed: {
        Screen_width () {
            return uni.getSystemInfoSync().windowWidth;
        }
    },
    data () {
        return {
            visible: false,
            start_slide_x: 0,
            btnWidth: 0,
            startX: 0,
            LastX: 0,
            startTime: 0,
            pageInfo: {
                total: 0,
                page: 1,    // 当前页码
                limit: 10,  // 每页条数
            },  // 页码传参数
            list: [
                {
                    id: 1,
                    studentName: '学霸',
                    className: '一年级（2）班',
                    studentName: '吴美丽',
                    dateTime: '2019-03-18',
                    schoolName: '广东省佛山市第7小学',
                    isShare: true,
                    slide_x: 0
                },
				{
				    id: 1,
				    studentName: '天秀',
				    className: '二年级（2）班',
				    studentName: '吴学究',
				    dateTime: '2019-03-18',
				    schoolName: '广东省佛山市第7小学',
				    isShare: true,
				    slide_x: 0
				},
            ],
            secondHeight: ''
        };
    },
    onShow () {
        const res = uni.getSystemInfoSync();
        // 计算主体部分高度,单位为px
        this.secondHeight = res.windowHeight
    },
    methods: {
		search (res) {
		    uni.showModal({
		        content: '搜索：' + res.value,
		        showCancel: false
		    })
		},
		
        /**
		* 获取列表
		* @return {type} {description}
		*/
        handleSearch () {
            boundChildList(this.pageInfo).then(res => {
                if (res[1].data.code == 0) {
                    res[1].data.data.records.forEach(ele => {
                        ele.slide_x = 0
                    });
                    this.list = res[1].data.data.records
                }
            }).finally(() => { })
        },

        /**
         * 解绑孩子
         * @return {type} {description}
         */
        handleRemoveM (index, id) {
            let self = this
            uni.showModal({
                title: '',
                content: '确定要解绑该孩子信息吗？',
                confirmText: '解绑',
                confirmColor: '#ff3b32',
                success: function (res) {
                    if (res.confirm) {
						uni.showToast({
						    icon: "success",
						    title: '解绑成功!',
						    duration: 2000
						});
                        // removeBindChildren(id).then(res => {
                        //     if (res[1].data.code == 0) {
                        //         uni.showToast({
                        //             icon: "success",
                        //             title: '解绑成功!',
                        //             duration: 2000
                        //         });
                        //         self.handleSearch()
                        //     }
                        // }).finally(() => { })
                    } else if (res.cancel) {
                        console.log('用户点击取消')
                    }
                }
            });
        },

        // =============================================================================
        getDetail (item) {
            console.log('查看详情')
        },
        // 滑动开始
        touchStart (e, index) {
            //记录手指放上去的时间
            this.startTime = e.timeStamp;
            //记录滑块的初始位置
            this.start_slide_x = this.list[index].slide_x;
            // 按钮宽度
            uni.createSelectorQuery()
                .selectAll('.group-btn')
                .boundingClientRect()
                .exec(res => {
                    if (res[0] != null) {
                        this.btnWidth = res[0][index].width * -1;
                    }
                });
            // 记录上一次开始时手指所处位置
            this.startX = e.touches[0].pageX;
            // 记录上一次手指位置
            this.lastX = this.startX;
            //初始化非当前滑动消息列的位置
            this.list.forEach((item, eq) => {
                if (eq !== index) {
                    item.slide_x = 0;
                }
            });
        },
        // 滑动中
        touchMove (e, index) {
            const endX = e.touches[0].pageX;
            const distance = endX - this.lastX;
            // 预测滑块所处位置
            const duang = this.list[index].slide_x + distance;
            // 如果在可行区域内
            if (duang <= 0 && duang >= this.btnWidth) {
                this.list[index].slide_x = duang;
            }
            // 此处手指所处位置将成为下次手指移动时的上一次位置
            this.lastX = endX;
        },
        // 滑动结束
        touchEnd (e, index) {
            let distance = 10;
            const endTime = e.timeStamp;
            const x_end_distance = this.startX - this.lastX;
            if (Math.abs(endTime - this.startTime) > 200) {
                distance = this.btnWidth / -2;
            }
            // 判断手指最终位置与手指开始位置的位置差距
            if (x_end_distance > distance) {
                this.list[index].slide_x = this.btnWidth;
            } else if (x_end_distance < distance * -1) {
                this.list[index].slide_x = 0;
            } else {
                this.list[index].slide_x = this.start_slide_x;
            }
        },
        // 点击回复原状
        recover (index) {
            this.list[index].slide_x = 0;
        },
        // 分享
        top (id) {
            console.log('点击分享')
            if (this.visible) {
                this.visible = false
            } else {
                this.visible = true
            }
        },
        // 路由跳转
        handleGoRouter (url) {
            uni.navigateTo({
                url: url,
                success: res => { },
                fail: () => { },
                complete: () => { }
            })
        }
    },
    mounted () {
        // this.handleSearch()
    },
};
